import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import VideoDetail from './views/VideoDetail.vue'


Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/videoDetail/:videoId',
            name: '详情',
            component: VideoDetail,
            props: true
        }
    ]
})
