import service from '../util/Request';


let VideoApi = {
    getVideosByType(type, page) {
        return new Promise((resolve) => {
            return service.get(type + "/" + page).then(res => {
                resolve(res.data);
            });
        });
    },
    getVideoDetail(videoId) {
        return new Promise((resolve) => {
            return service.get("detail/" + videoId).then(res => {
                resolve(res.data);
            });
        });
    },
    getVideoPlayUrl(videoId, seriesId) {
        return new Promise((resolve) => {
            return service.get("playUrl/" + videoId + "/" + seriesId).then(res => {
                resolve(res.data);
            });
        });
    }
}
export default VideoApi
