<template>
    <div class="hello">
        <el-container>
            <el-main>
                <h1>
                    <el-row>
                        <el-col v-for="video in videos" :sm="4" :xs="4">
                            <div style="position:relative; height: 20vw;margin: 0.2vw" @click="goDetail(video)">
                                <div style="width: 100%;height: 85%;">
                                    <img :src="video.thumb" style="height:100%;">
                                </div>
                                <div style="width: 100%;height: 15%;position: relative;overflow:hidden;text-align:center;line-height:100%">
                                    <h6 style="margin: 0;font-size: 0.1vw">{{video.title}}</h6>
                                </div>
                            </div>
                        </el-col>
                    </el-row>
                </h1>
            </el-main>
            <el-footer>
                <el-pagination @current-change="handleChange"
                               background
                               layout="prev, pager,   next"
                               :page-count=30>
                </el-pagination>
            </el-footer>
        </el-container>
    </div>
</template>

<script>

    import VideoApi from '../api/VideoApi'

    export default {
        props: {
            playType: "",
        },
        data() {
            return {
                videos: [],
            };
        },
        methods: {
            getVideo(page) {
                VideoApi.getVideosByType(this.playType, page).then(res => {
                    this.videos = res;
                });
            },
            goDetail(video) {
                console.log(video.videoId)
                this.$router.push("/videoDetail/" + video.videoId)
            },
            handleChange(value) {
                this.getVideo(value);
            }
        },
        mounted() {
            this.getVideo(1);
        }
    }
</script>

